<template>
  <div class="col-12">
    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Store</strong> Customers <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
        <b-button id="btn566" type="submit" size="sm" variant="warning" class="pull-right" @click="downloadExport()"><i class="fa fa-arrow-down"></i> Export</b-button>
        <a href="https://docs.range-pro.com/store_account.html#customers" target="new" class="button">
          <b-button id="btn567" type="submit" size="sm" variant="primary" class="pull-right mr-1"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </div>

      <b-row>
        <b-col md="6" class="my-1">
          <b-form-group horizontal label="Search" class="mb-3">
            <b-input-group>
              <b-form-input v-model="filter" placeholder="Type to Search" />
              <b-input-group-append>
                <b-btn id="btn568" :disabled="!filter" @click="filter = ''">Clear</b-btn>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>
      </b-row>

      <b-table striped hover
               :current-page="currentPage"
               :per-page="perPage"
               :filter="filter"
               :items="item_list"
               :fields="fields">

        <template slot="id" slot-scope="data">

        </template>

      </b-table>

      <nav>
        <b-pagination :total-rows="getRowCount(item_list)" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
      </nav>

    </b-card>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'

  export default {
    name: 'StoreCustomers',
    data () {
      return {
        item_list: [],
        loading: true,
        filter: null,
        currentPage: 1,
        perPage: 5,
        totalRows: 0,
        fields: [
          {
            key: 'first_name',
            label: 'First Name'
          },
          {
            key: 'last_name',
            label: 'Last Name',
            sortable: true
          },
          {
            key: 'phone',
            label: 'Phone'
          },
          {
            key: 'email',
            label: 'Email'
          }
        ]
      }
    },
    mounted: function () {
      this.getData()
    },
    methods: {
      getData (response) {
        rangepro.get_secure('store/marketing/customers', this.handleResponse, 'GetStoreMarketingCustomers')
      },
      downloadExport () {
        rangepro.get_download_secure('store/marketing/customers/export', 'export.csv', this.handleResponse, 'GetStoreMarketingCustomers')
      },
      handleResponse (data, success) {
        if (success) {
          this.item_list = data.customers
          this.loading = false
        } else {
          this.loading = false
        }
      },
      getRowCount (items) {
        return items.length
      }
    }
  }
</script>

<style scoped>

</style>



